// components/AdminStateExplorer.tsx
import React, { useState } from 'react';
import dynamic from 'next/dynamic';
import Link from 'next/link';
import type { RegionId } from './GlobeStates';

const GlobeStates = dynamic(() => import('./GlobeStates'), {
  ssr: false,
  loading: () => (
    <div className="flex h-full w-full items-center justify-center text-xs text-slate-500">
      Loading 3D atlas…
    </div>
  ),
});

const regionLabels: { id: RegionId; label: string }[] = [
  { id: 'unitedStates', label: 'United States' },
  { id: 'canada', label: 'Canada' },
  { id: 'mexico', label: 'Mexico' },
  { id: 'netherlands', label: 'Netherlands' },
];

const US_STATES: { id: string; name: string }[] = [
  { id: 'AL', name: 'Alabama' },
  { id: 'AK', name: 'Alaska' },
  { id: 'AZ', name: 'Arizona' },
  { id: 'AR', name: 'Arkansas' },
  { id: 'CA', name: 'California' },
  { id: 'CO', name: 'Colorado' },
  { id: 'CT', name: 'Connecticut' },
  { id: 'DE', name: 'Delaware' },
  { id: 'FL', name: 'Florida' },
  { id: 'GA', name: 'Georgia' },
  { id: 'HI', name: 'Hawaii' },
  { id: 'ID', name: 'Idaho' },
  { id: 'IL', name: 'Illinois' },
  { id: 'IN', name: 'Indiana' },
  { id: 'IA', name: 'Iowa' },
  { id: 'KS', name: 'Kansas' },
  { id: 'KY', name: 'Kentucky' },
  { id: 'LA', name: 'Louisiana' },
  { id: 'ME', name: 'Maine' },
  { id: 'MD', name: 'Maryland' },
  { id: 'MA', name: 'Massachusetts' },
  { id: 'MI', name: 'Michigan' },
  { id: 'MN', name: 'Minnesota' },
  { id: 'MS', name: 'Mississippi' },
  { id: 'MO', name: 'Missouri' },
  { id: 'MT', name: 'Montana' },
  { id: 'NE', name: 'Nebraska' },
  { id: 'NV', name: 'Nevada' },
  { id: 'NH', name: 'New Hampshire' },
  { id: 'NJ', name: 'New Jersey' },
  { id: 'NM', name: 'New Mexico' },
  { id: 'NY', name: 'New York' },
  { id: 'NC', name: 'North Carolina' },
  { id: 'ND', name: 'North Dakota' },
  { id: 'OH', name: 'Ohio' },
  { id: 'OK', name: 'Oklahoma' },
  { id: 'OR', name: 'Oregon' },
  { id: 'PA', name: 'Pennsylvania' },
  { id: 'RI', name: 'Rhode Island' },
  { id: 'SC', name: 'South Carolina' },
  { id: 'SD', name: 'South Dakota' },
  { id: 'TN', name: 'Tennessee' },
  { id: 'TX', name: 'Texas' },
  { id: 'UT', name: 'Utah' },
  { id: 'VT', name: 'Vermont' },
  { id: 'VA', name: 'Virginia' },
  { id: 'WA', name: 'Washington' },
  { id: 'WV', name: 'West Virginia' },
  { id: 'WI', name: 'Wisconsin' },
  { id: 'WY', name: 'Wyoming' },
];

type SnapshotKey = 'default' | 'us-CA';

interface StateSnapshot {
  title: string;
  batchesLabel: string;
  labsLabel: string;
  recallsLabel: string;
  batchesValue: string;
  labsValue: string;
  recallsValue: string;
  coverageLabel: string;
  coverageValue: string;
  coveragePercent?: number;
  footnote: string;
}

const SNAPSHOTS: Record<SnapshotKey, StateSnapshot> = {
  'us-CA': {
    title: 'California',
    batchesLabel: 'Batches',
    labsLabel: 'Labs',
    recallsLabel: 'Recalls',
    batchesValue: '18,234',
    labsValue: '97',
    recallsValue: '12',
    coverageLabel: 'Coverage',
    coverageValue: '82%',
    coveragePercent: 82,
    footnote:
      '82% estimated coverage of the legal market in this state.',
  },
  default: {
    title: 'Coming soon',
    batchesLabel: 'Batches',
    labsLabel: 'Labs',
    recallsLabel: 'Recalls',
    batchesValue: '—',
    labsValue: '—',
    recallsValue: '—',
    coverageLabel: 'Coverage',
    coverageValue: 'Coming soon',
    coveragePercent: undefined,
    footnote:
      'Coverage metrics will appear here as soon as CartFax is connected to this market.',
  },
};

interface SelectedState {
  id: string;
  name: string;
  region: RegionId;
}

const getSnapshotKey = (selected: SelectedState | null): SnapshotKey => {
  if (!selected) return 'default';
  if (selected.region === 'unitedStates' && selected.id === 'CA') {
    return 'us-CA';
  }
  return 'default';
};

const AdminStateExplorer: React.FC = () => {
  const [region, setRegion] = useState<RegionId>('unitedStates');
  const [selected, setSelected] = useState<SelectedState | null>({
    id: 'CA',
    name: 'California',
    region: 'unitedStates',
  });
  const [viewAltitude, setViewAltitude] = useState<number | null>(null);

  const zoomedIn = viewAltitude !== null && viewAltitude < 1.6;
  const quickStates =
    zoomedIn && region === 'unitedStates' ? US_STATES : [];

  const isUS = region === 'unitedStates';

  const handleRegionChange = (next: RegionId) => {
    setRegion(next);
    // Keep the last selection, but make sure its region is updated
    setSelected(prev =>
      prev ? { ...prev, region: next } : null,
    );
  };

  const handleStateSelect = (id: string | null, name: string | null) => {
    if (!id || !name) return;
    setSelected({
      id: id.toUpperCase(),
      name,
      region,
    });
  };

  const snapshotKey = getSnapshotKey(selected);
  const snapshot = SNAPSHOTS[snapshotKey];

  const handleDismissCard = () => {
    setSelected(null);
  };

  return (
    <main className="min-h-screen bg-black text-slate-100">
      <div className="flex flex-col px-8 pb-10 pt-6">
        {/* Top-left status */}
        <div className="mb-4 flex flex-wrap items-baseline gap-3">
          <p className="text-[11px] font-semibold uppercase tracking-[0.22em] text-sky-400">
            {isUS ? 'US · States' : 'Global · Regions'}
          </p>
          <p className="text-xs text-slate-400">
            {selected?.name
              ? `${selected.id} · ${selected.name} — metallic glass tiles punched out of a spinning globe.`
              : 'Spin the globe and tap any market to open its safety & COA snapshot.'}
          </p>
        </div>

        {/* Centered region tabs + hint */}
        <div className="mb-6 flex flex-col items-center gap-3">
          <div className="inline-flex rounded-full border border-slate-800 bg-slate-900/80 px-1 py-1 shadow-[0_0_40px_rgba(56,189,248,0.35)]">
            {regionLabels.map(r => (
              <button
                key={r.id}
                type="button"
                onClick={() => handleRegionChange(r.id)}
                className={
                  'rounded-full px-4 py-1.5 text-[11px] transition ' +
                  (region === r.id
                    ? 'bg-sky-500 text-white shadow-sm shadow-sky-500/60'
                    : 'text-slate-400 hover:text-slate-100')
                }
              >
                {r.label}
              </button>
            ))}
          </div>
          <div className="flex items-center gap-2 text-[11px] text-slate-500">
            <span className="inline-flex h-2 w-2 rounded-full bg-sky-400 shadow-[0_0_0_5px_rgba(56,189,248,0.55)]" />
            <span>
              Drag to spin · Hover to “lift” · Scroll to zoom · Click to drill
              in
            </span>
          </div>
        </div>

        {/* Globe centered on the screen */}
        <div className="mt-4 flex w-full items-center justify-center">
          <div className="relative aspect-square w-[min(70vh,80vw)]">
            <div className="absolute inset-0">
              <GlobeStates
                region={region}
                onRegionChange={handleRegionChange}
                onStateSelect={(id, name) => handleStateSelect(id, name)}
                onViewChange={alt => setViewAltitude(alt)}
              />
            </div>
          </div>
        </div>
      </div>

      {/* Quick-jump overlay for US only, when zoomed in */}
      {quickStates.length > 0 && (
        <aside className="fixed left-6 top-1/2 z-30 hidden max-h-[420px] w-60 -translate-y-1/2 rounded-2xl border border-slate-800 bg-slate-950/95 px-3 py-3 text-[11px] text-slate-200 shadow-[0_18px_60px_rgba(15,23,42,0.85)] backdrop-blur lg:block">
          <p className="mb-2 px-1 text-[10px] font-semibold uppercase tracking-[0.18em] text-slate-500">
            Quick jump
          </p>
          <div className="max-h-[360px] overflow-y-auto pr-1">
            {quickStates.map(s => (
              <button
                key={s.id}
                type="button"
                onClick={() =>
                  setSelected({
                    id: s.id,
                    name: s.name,
                    region: 'unitedStates',
                  })
                }
                className={
                  'mb-[3px] w-full rounded-full px-2 py-1 text-left transition ' +
                  (selected?.id === s.id
                    ? 'bg-sky-500 text-white'
                    : 'bg-slate-900 text-slate-200 hover:bg-slate-800')
                }
              >
                {s.id} · {s.name}
              </button>
            ))}
          </div>
        </aside>
      )}

      {/* Floating detail panel */}
      {selected && (
        <div className="fixed bottom-6 right-6 z-30 max-w-sm rounded-2xl border border-slate-800 bg-slate-950/95 px-4 py-4 text-sm text-slate-100 shadow-[0_22px_70px_rgba(15,23,42,0.85)] backdrop-blur">
          <p className="mb-1 text-[11px] font-semibold uppercase tracking-[0.22em] text-sky-400">
            {selected.id} · {isUS ? 'State snapshot' : 'Region snapshot'}
          </p>
          <p className="mb-3 text-base font-semibold">
            {snapshot.title === 'Coming soon'
              ? selected.name
              : snapshot.title}
          </p>
          <p className="mb-4 text-xs text-slate-400">
            {snapshot.title === 'Coming soon'
              ? `We don’t have a full snapshot wired up for ${selected.name} yet. In production, this panel will show live batch safety, lab coverage, and recall activity as soon as CartFax is connected to this market.`
              : 'Live-style safety snapshot for California. In production this panel will connect directly to CartFax batch, COA, lab, and recall streams.'}
          </p>

          <div className="mb-4 grid grid-cols-3 gap-2 text-[11px]">
            <div className="rounded-xl bg-slate-900/80 px-3 py-2">
              <p className="mb-1 text-[10px] uppercase tracking-[0.16em] text-slate-500">
                {snapshot.batchesLabel}
              </p>
              <p className="text-sm font-semibold text-slate-50">
                {snapshot.batchesValue}
              </p>
            </div>
            <div className="rounded-xl bg-slate-900/80 px-3 py-2">
              <p className="mb-1 text-[10px] uppercase tracking-[0.16em] text-slate-500">
                {snapshot.labsLabel}
              </p>
              <p className="text-sm font-semibold text-slate-50">
                {snapshot.labsValue}
              </p>
            </div>
            <div className="rounded-xl bg-slate-900/80 px-3 py-2">
              <p className="mb-1 text-[10px] uppercase tracking-[0.16em] text-slate-500">
                {snapshot.recallsLabel}
              </p>
              <p className="text-sm font-semibold text-slate-50">
                {snapshot.recallsValue}
              </p>
            </div>
          </div>

          <div className="mb-3">
            <div className="mb-1 flex items-center justify-between text-[10px]">
              <span className="uppercase tracking-[0.16em] text-slate-500">
                {snapshot.coverageLabel}
              </span>
              <span className="font-semibold text-slate-50">
                {snapshot.coverageValue}
              </span>
            </div>
            <div className="h-2 w-full overflow-hidden rounded-full bg-slate-900">
              <div
                className="h-full rounded-full bg-sky-500"
                style={{
                  width: snapshot.coveragePercent
                    ? `${snapshot.coveragePercent}%`
                    : '0%',
                }}
              />
            </div>
          </div>

          <p className="mb-4 text-[10px] text-slate-500">
            {snapshot.footnote}
          </p>

          <div className="flex items-center justify-between gap-3">
            <Link
              href="/admin"
              className="inline-flex items-center justify-center rounded-full bg-sky-500 px-4 py-1.5 text-xs font-medium text-white shadow-sm shadow-sky-500/50 transition hover:bg-sky-400"
            >
              Open admin data tools
            </Link>
            <button
              type="button"
              onClick={handleDismissCard}
              className="text-[11px] text-slate-500 hover:text-slate-300"
            >
              Dismiss
            </button>
          </div>
        </div>
      )}
    </main>
  );
};

export default AdminStateExplorer;
